<template>
  <div class="powerFailureMonitoring">
    <!-- <div class="title">客户停电状态监控</div> -->
    <div class="nav">
      <el-input
        class="inputC"
        placeholder="请输入来电号码或者客户编号"
        v-model="selectValue"
        size="small"
        clearable
      ></el-input>
      <el-button size="small" type="primary" @click="getIntentionList">查询</el-button>
    </div>
    <div class="table-con">
      <bdaTable
          v-loading="loading"
        :tableData="outageStatusData"
        :isPage="true"
        :pageConfig="outageStatusPageConfig"
        @changePage="changePage"
      ></bdaTable>
    </div>
  </div>
</template>

<script>
  import Interface from '@/base/mixin/outage-sensitive';
import bdaTable from '@/components/bda-table/bda-table'

export default {
  name: "powerFailureMonitoring",
  mixins: [Interface],
  components: {
    bdaTable
  },
  props: {

  },
  data() {
    return {
      // 停电状态
      outageStatusData: {
        tableData: [],
        columnNameData: [
          { prop: 'yhbh', label: '用户编号' },
          { prop: 'type', label: '类型' },
          { prop: 'state', label: '计量装置状态' },
          { prop: 'createTime', label: '计量装置状态采集时间' },
          // { prop: 'updateName', label: '停电事件记录' },
          { prop: 'updateTime', label: '实际停电开始时间' },
        ]
      },
      outageStatusQueryForm: {
        menuType: '9',
        tagName: "",
        current: 1,
        size: 10,
      },
      //分页设置
      outageStatusPageConfig: {
        name: "outageStatus",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40, 50],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      selectValue: '',
      loading: false,
    }
  },
  mounted() {
    this.getIntentionList()
  },
  methods: {
    //改变分页
    changePage(data) {
      if (data.name === 'outageStatus') {
        this.outageStatusQueryForm.size = data.size
        this.outageStatusQueryForm.current = data.current
        this.getIntentionList()
      }
    },
    // 停电状态列表
    getIntentionList() {
      this.loading = true
      this.getOutageInfoForOther({
        "keyword":this.selectValue,
        current: this.outageStatusQueryForm.current,
        size: this.outageStatusQueryForm.size
      }).then(res => {
        this.loading = false
        let columnNameData = [
          { prop: 'yhbh', label: '用户编号'},
          { prop: 'tdlx', label: '类型' },
          { prop: 'jlzzzt', label: '计量装置状态', width:'120' },
          { prop: 'jlzzztcjsj', label: '计量装置状态采集时间' },
          // { prop: 'tdsjczsj', label: '停电事件记录' },
          { prop: 'tdsjsjkssj', label: '实际停电开始时间' },
        ]
        this.outageStatusPageConfig.total = res.total
        this.outageStatusPageConfig.size = this.outageStatusQueryForm.size
        this.outageStatusPageConfig.current = this.outageStatusQueryForm.current
        this.outageStatusData = {
          tableData: [
            ...res.records
          ],
          headerColor: '#1899fa',
          title: "停电状态列表",
          name: 'outageStatus',
          size: this.outageStatusQueryForm.size,
          current: this.outageStatusQueryForm.current,
          total: this.outageStatusPageConfig.total,
          columnNameData: columnNameData
        }

      })
    },
  }
}
</script>

<style scoped lang="scss">
.powerFailureMonitoring {
  ::v-deep .el-table {
    .success-row {
      background: rgb(244, 244, 244);
    }
    .cell {
      padding: 2px 4px;
    }
    th {
      background-color: rgb(24, 153, 250);
      .cell {
        color: #ffffff;
      }
    }
  }
  .inputC {
    width: calc(100% - 300px);
    max-width: 600px;
    margin: 0px 6px;
  }
  & > div {
    margin: 10px 0px;
  }
  .title {
    padding-left: 18px;
    border-left: 6px solid #1a84f4;
    font-weight: bold;
    color: #1a84f4;
  }
  .nav {
    text-align: center;
    span {
      margin: 0px 8px 0px 30px;
    }
  }
  .table-con {
    height: 400px;
  }
}
</style>